.header-container{
  padding: 0 3vw;
  position: relative;

  #header-text {
    padding: 0 0;
    margin: 0 0;
    color: #222222;
  }

  position: relative;
  padding: 0 3vw;

  .header-menu{
    list-style: none;
    padding: 0;
    position: absolute;
    left: 0;
    top: 100px;

    letter-spacing: 1px;
    font-family: 'Play', sans-serif;
    color: #626262;
    font-weight: 700;
    text-transform: uppercase;
    
    li {
      margin: 10px 0;
    }
  }

  h1 {
    font-size: 16vw;
    text-transform: uppercase;
    text-align: center;

    font-family: 'Bai Jamjuree', sans-serif;
    font-weight: 600;
   
   
  }
}
.lineParent{
  overflow: hidden;
  .lineChildren{
    transform: translate(0, 500px);
  }
}