$bg-color: #f5f0ec;

body {
  font-family: "Poppins", sans-serif;
  margin: 0 0;
  padding: 0 0;
  cursor: none;
  background-color: $bg-color;
}

.main-container {
  padding: 0 2vw;
  // clip-path: inset(100% 0% 0% 0%);
  // backface-visibility: hidden;
}

section {
  padding: 100px 0;
  .section-header {
    margin-bottom: 50px;
  }
}
.absolute {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: black;
  z-index: 1;
}

.loader-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: "all 0.25s ease-in-out";
  background-color: #191919;
  color: #dbd8d6;
  h1 {
    font-size: 2vw;
    text-transform: uppercase;
    font-family: "Play", sans-serif;
    font-weight: 700;
  }
  h2 {
    font-size: 2vw;

    text-transform: uppercase;
    font-family: "Play", sans-serif;
    margin-top: 0;
  }
}
